<template>
  <div>
    <b-form-group v-slot="{ ariaDescribedby }">
      <b-form-radio-group
        id="btn-radios"
        v-model="category"
        :options="options"
        :aria-describedby="ariaDescribedby"
        button-variant="outline-primary"
        size="lg"
        name="radios-btn-outline"
        buttons
      ></b-form-radio-group>
    </b-form-group>
    <!-- Using components -->
    <b-input-group prepend="Username" class="mt-3">
      <b-form-input v-model="name"></b-form-input>
      <b-form-input v-model="ticker"></b-form-input>
      <b-form-input v-model="price"></b-form-input>
      <b-form-input v-model="amount"></b-form-input>
      <b-input-group-append>
        <b-button variant="info" v-on:click="send">Send</b-button>
      </b-input-group-append>
    </b-input-group>
  </div>
</template>

<script>
export default {  
    data() {
      return {
        category: 'buy',
        options: [
          { text: '매수', value: 'buy' },
          { text: '매도', value: 'sell' },
          { text: '입금', value: 'deposit' },
          { text: '출금', value: 'withdraw' },
          { text: '배당', value: 'dividend' },
          { text: '이자', value: 'interest' }
        ],
        name: '',
        category: '',
        ticker: '',
        price: 0,
        amount: 0,
      }
    },
    methods: {
      send(){
        this.$http.put('/api/order',{
            name : this.name,
            category: this.category,
            ticker: this.ticker,
            price: this.price,
            amount: this.amount
          }).then((response) => {
            console.log(response);
        })
      }
    }
}
</script>

<style>

</style>


